import React from "react";
import styled from "styled-components";

import { WunderkindPatternProps } from "./Pattern";

export const PatternWrapperSvg = styled.svg<WunderkindPatternProps>`
  	display: block;
	${({ backgroundColor }) => backgroundColor && `background-color: ${backgroundColor};`};
`;

export interface WunderkindPatternWrapperProps extends WunderkindPatternProps {
	children: React.ReactElement | React.ReactElement[];
}

const PatternWrapper = ({
	name,
	backgroundColor,
	patternColor,
	viewBox,
	children,
}: WunderkindPatternWrapperProps) => (
	<PatternWrapperSvg
		name={name}
		data-qa={`${name}-pattern`}
		xmlns="http://www.w3.org/2000/svg"
		backgroundColor={backgroundColor}
		patternColor={patternColor}
		viewBox={viewBox}
	>
		{children}
	</PatternWrapperSvg>
);

PatternWrapper.displayName = "PatternWrapper";
export default PatternWrapper;
